rea\" \n      v-el=\"newTask\"\n      v-model=\"newTask\"></textarea>\n\n    <span v-show=\"!showTextarea\" \n      v-on=\"click:toggleTextarea\" \n      v-transition=\"bounce\"  \n      v-class=\"addTaskBtn:!showTextarea\">\n      <img v-show=\"!showTextarea\" src=\"" + require("./../../icon/add.png") + "\" alt=\"add\">\n    </span>\n\n    <span v-show=\"showTextarea\" \n      v-on=\"click:addNewTask\" \n      v-transition=\"bounce\"  \n      v-class=\"saveTaskBtn:showTextarea\">\n      <img v-show=\"showTextarea\" src=\"" + require("./../../icon/save.png") + "\" alt=\"save\">\n    </span>\n\n    <span v-show=\"showTextarea\" \n      v-on=\"click:cancelNewTask\"\n      v-transition=\"bounce\"  \n      v-class=\"cancelTaskBtn:showTextarea\">\n      <img v-show=\"showTextarea\" src=\"" + require("./../../icon/cancel.png") + "\" alt=\"cancel\">\n    </span>\n  </div>\n  <footerinfo></footerinfo>\n  <!-- <pre>{{$data | json}}</pre> -->\n</div>";


