var api = require('./../api.js')
  module.exports = {
    data:function(){
      return{
        email:'',
        password:'',
        showerror:false
      }
    },
    methods: {
      getLogin: function(e){
        e.preventDefault()
        var data = {
        email: this.email,
        password: this.password
        }
        api.user.login(data,function(res){
          // this.$root.view = "person"
          console.log(res)
          if(res  && res.email){
            this.email = this.password = ''
            window.location.hash = '/person'
          }else{
            this.showerror = true
            setTimeout(function(){
              this.showerror = false
            }.bind(this),2000)
          }
        }.bind(this))
      },
      close: function(){
        window.location.hash = '/'
      }
    }
  }


